// Banner
.banner {
  background: url(../img/banner.jpg) 50% 50% no-repeat;
  background-size: cover;
  color: $white;
}
.banner-heading {
  @extend .h-white;
  text-transform: uppercase;
  font-weight: 700;
  margin: .7em 0;
}
.banner-buttons {
  margin-top: 40px;
  display: flex;
  justify-content: center;
  .button { margin: 0 4px; }
}