// Map and Location
.event-location-block {
  background: $black;
  padding-bottom: 0;
}
.map {
  width: 100%;
  overflow: hidden;
  position: relative;
}
.google-map {
  height: 400px;
  width: 100%;
}
.location-box {
  background-color: $white;
  position: absolute;
  width: 300px;
  height: 150px;
  top: 50%;
  left: 50%;
  margin-top: -75px;
  margin-left: -150px;
  z-index: 9990;
  @include breakpoint(m) {
    top: auto;
    bottom: 0;
    left: 0;
    margin-top: 0;
    margin-left: 0;
  }
}
.location { padding-bottom: 1em; }

.location-box-container {
  @include breakpoint(m) {
    position: absolute;
    left: 50%;
    left: 2rem;
    bottom: 2em;
  }
}